.container {
        display: grid;
        /* grid-gap: 1rem; */
        grid-template-columns: 0.5fr 4fr;
        grid-template-rows: 0.5fr 1fr 3fr 3fr 3fr 3fr;
        grid-template-areas: 'header image '
                             'header about'
                             'header skills'
                             'header project'
                             'header contact';
        background-color: whitesmoke;
}
.header {
    grid-area: header;
    border-right: solid black;
}

.about {
	grid-area: about;
}

.skills {
	grid-area: skills;
}

.project {
	grid-area: project;
}

.contact {
	grid-area: contact;
}
.title {
    background-color:rgb(128, 128, 128);
    color: black;
    text-align: left;
}

#image {
  background-image: linear-gradient(rgba(0,0,0,0.7), rgba(0,0,0,0.7)),
  url("./images/self.jpg");
  /* background-size: cover; */
  width: 80vw;
  height: 50vh;
  position: relative;
  float: right;
  color: white;
}